.wrapper {
    background: $color-white;
}
.login {
    text-align: center;
    h2 {
        @include rem((margin: 20px 0));
    }
    button {
        @include rem((margin: 20px 0));
    }
}
button.btn-login {
    @extend %ghost-button-green;
}
button.btn-logout {
    @extend %ghost-button-red;
}
.loggedin {
    @include rem((margin-top: 50px));
    @include rem((margin-bottom: 50px));
    h2 {
        @include rem((marin: 20px 0));
    }
}
